/* App.css */

body {
  margin: 0;
  font-family: Arial, sans-serif;
  background-color: #f4f4f4;
  color: #333;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

header {
  background-color: #007BFF;
  color: white;
  text-align: center;
  padding: 20px;
}

header h1 {
  font-size: 2rem;
  margin: 0;
}

main {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 20px;
}

.cover {
  border-radius: 10px;
  width: 100%;
  max-width: 300px;
  height: auto;
  margin-bottom: 20px;
}

.song-info {
  text-align: center;
}

.song-info h2 {
  font-size: 1.5rem;
  margin: 10px 0;
}

.song-info p {
  font-size: 1rem;
  color: #555;
}

audio {
  margin-top: 20px;
  width: 100%;
  max-width: 300px;
}

footer {
  background-color: #333;
  color: white;
  text-align: center;
  padding: 10px;
  margin-top: auto;
}

footer p {
  margin: 0;
  font-size: 0.9rem;
}

/* Responsiveness */
@media (max-width: 768px) {
  header h1 {
    font-size: 1.5rem;
  }

  .song-info h2 {
    font-size: 1.25rem;
  }

  .song-info p {
    font-size: 0.9rem;
  }
}
